<template>
  <div class="advise">
    <!--  -->
    <el-breadcrumb separator="/">
    		<el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
    		<el-breadcrumb-item><a href="/"></a></el-breadcrumb-item>
    		<el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>	
    <!--  -->
    <div class="advisehead">
    	<div class="advisecoat">
    		<el-select v-model="value" clearable placeholder="">
    	<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
    	</el-option>
    		</el-select>
    		<el-button type="primary"></el-button>
    	</div>
    </div>
    <!--  -->
    <el-table :data="tableData" border style="width: 100%;" @current-change="idHandleCurrentChange" >
    <el-table-column type="index" label=""  width="65"></el-table-column>
    <el-table-column prop="feedbackusername" label="" width="115" ></el-table-column>	
    <el-table-column prop="feedbackname" label=""  width="115"></el-table-column>
    <el-table-column prop="identity" label="" width="115"></el-table-column>
    <el-table-column prop="idea" label=""  width="115"></el-table-column>
    <el-table-column prop="type" label=""  width="115"></el-table-column>
    <el-table-column prop="ideatime" label="" width="125"></el-table-column>
    <el-table-column prop="" label=""  >
	    <template slot-scope="scope">
	    	<el-button size="mini" type="info" @click="findschool(scope.$index)"></el-button>
	    	<el-button size="mini" @click="editschool(scope.$index)"></el-button>
	    	<el-button size="mini" type="danger" @click="delschool(scope.$index)"></el-button>	
	    </template>
    </el-table-column>
    </el-table>
    
    <!--  -->
    <div class="advisefoot">
    	<div class="block">
    		<el-pagination @size-change="handleSizeChange"
    			@current-change="handleCurrentChange"
    			:current-page="currentPage4"
    			:page-sizes="[10, 20, 30]"
    			:page-size="10"
    			layout="total, sizes, prev, pager, next, jumper"
    			:total="3">
    		</el-pagination>
    	</div>
    </div>	
    
    <!--  -->
    
    <div class="schoolbg" v-show="bgstate"></div>
    	
    	
    	<!--  -->
    	<div class="find"  v-show="findstate">
    		<h5><p></p><i class="el-icon-close" @click="closefind"></i></h5>
    			<ul >
    				<li><label></label>{{schoolmsg.feedbackusername}}</li>
    				<li><label></label>{{schoolmsg.feedbackname}}</li>
    				<li><label></label>{{schoolmsg.identity}}</li>
				<li><label></label>{{schoolmsg.idea}}</li>
				<li><label></label>{{schoolmsg.type}}</li>
				<li><label></label>{{schoolmsg.ideatime}}</li>
    				<li><label style="float:left;">logo</label>
    					<img src="../../static/vue.png" />
    				</li>
    				<div class="schoolfindbtn">
    					<el-button type="primary"></el-button>
    					<el-button type="danger" @click="deleteschool"></el-button>
    				</div>
    			</ul>
    	</div>	
    
    <!--  -->
    <div class="schooladdcoat" v-show="addstate">
    	<p><label></label><i class="el-icon-close" @click="canceledit"></i></p>
    	<el-form  :model="schoolmsg" :rules="rules" ref="ruleForm" label-width="110px" class="demo-ruleForm">
    		<el-form-item label="" prop="feedbackname" >
    			<el-input v-model="schoolmsg.feedbackname"></el-input>
    		</el-form-item>
		<el-form-item label="" prop="feedbackusername" >
			<el-input v-model="schoolmsg.feedbackusername"></el-input>
		</el-form-item>
		<el-form-item label="" prop="identity" >
			<el-input v-model="schoolmsg.identity"></el-input>
		</el-form-item>
		<el-form-item label="" prop="idea" >
			<el-input v-model="schoolmsg.idea"></el-input>
		</el-form-item>
    		<el-form-item label="" prop="type" >
    			<el-select v-model="schoolmsg.type" placeholder="">
    				<el-option label="" value=""></el-option>
    				<el-option label="" value=""></el-option>
    				<el-option label="" value=""></el-option>
    			</el-select>
    		</el-form-item>
    		<el-form-item label="" >
    			<el-form-item prop="ideatime">
    			<el-date-picker type="date" placeholder=""  format="yyyy  MM  dd "
      value-format="yyyy-MM-dd" v-model="schoolmsg.ideatime" style="width:94%;padding-right:20px;"></el-date-picker>
    			</el-form-item>
    		</el-form-item>
    				<div class="schoolbtncoat">
    					<el-button type="primary" @click="submitForm('ruleForm')"></el-button>
    					<el-button @click="resetForm('ruleForm')"></el-button>
    				</div>
    		</el-form>
    	</div>		
  </div>
</template>

<script>
export default {
  name: 'Advise',
  data () {
    return {
tableData:[],
		options: [{
	    value: '1',
	    label: ''
	  }, {
	    value: '2',
	    label: ''
	  }, {
	    value: '3',
	    label: ''
	  }, ],
	  value: '',
	  currentPage4: 1,
	  tempData:{},
	  tempindex:-1,
	  /*  */
	  findstate:false,
	  addstate:false,
	  bgstate:false,
	  sesname:"",
	  schoolmsg:{"id":"111","feedbackusername":"","feedbackname":"","identity":"","idea":"","type":"","ideatime":"2019-02-13"},
	  	 ruleForm: {
	      feedbackname: '',
	      ideatime: '',
	      feedbackusername:'',
	      identity:'',
	      idea:'',
	      type:''
	    },
	    rules: {
	      feedbackname: [
	        { required: true, message: '', trigger: 'change' },
	        { min: 3, max: 10, message: ' 3  10', trigger: 'change' }
	      ],
	      type: [
	        { required: true, message: '', trigger: 'change' }
	      ],
	      ideatime: [
	        { required: true, message: '', trigger: 'change' }
	      ],
	       feedbackusername: [
	        { required: true, message: '', trigger: 'change' },
			{pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/, message: ''}
	      ],
	       identity: [
	        { required: true, message: '', trigger: 'change' }
	      ],
	       idea: [
	        { required: true, message: '', trigger: 'change' }
	      ]
	  }
    }
  },
	methods:{
		/* ID*/
		 idHandleCurrentChange(val) {
		  this.currentRow = val;
		},
		handleSizeChange(val) {
		   console.log(` ${val} `);
		 },
		 handleCurrentChange(val) {
		   console.log(`: ${val}`);
		 },
		 /* */
		    deepCopy:function (obj) {
		    var result = Array.isArray(obj) ? [] : {};
		    for (var key in obj) {
		      if (obj.hasOwnProperty(key)) {
		        if (typeof obj[key] === 'object' && obj[key]!==null) {
		          result[key] = deepCopy(obj[key]);   //
		        } else {
		          result[key] = obj[key];
		        }
		      }
		    }
		    return result;
		  },
		  
		     /* */
		  findschool:function(index){
		  		  this.findstate=true;
		  		  this.bgstate=true;
		  		  this.tempindex=index;
		  		  this.schoolmsg=this.tableData[index];
		  },
		  closefind:function(){
		  		 this.findstate=false;
		  		 this.bgstate=false;
		  		 this.schoolmsg={"id":"111","feedbackusername":"","feedbackname":"","identity":"","idea":"","type":"","ideatime":"2019-02-13"};
		  },
		 /* */
		  deleteschool:function(){
		  	this.$confirm(', ?', '', {
		        confirmButtonText: '',
		        cancelButtonText: '',
		        type: 'warning'
		      }).then(() => {
		  		 this.tableData.splice(this.tempindex,1);
		  		this.findstate=false;
		  		this.bgstate=false;
		  		this.tempindex=-1;
		        this.$message({
		  		showClose: true,
		          type: 'success',
		          message: '!'
		        });
		      }).catch(() => {
		        this.$message({
		  		showClose: true,
		          type: 'info',
		          message: ''
		        });          
		      });
		  },
		   /* */
		  delschool:function(index){
		  		  this.$confirm(', ?', '', {
		      confirmButtonText: '',
		      cancelButtonText: '',
		      type: 'warning'
		    }).then(() => {
		  			this.tableData.splice(index,1);
		      this.$message({
		  			showClose: true,
		        type: 'success',
		        message: '!'
		      });
		    }).catch(() => {
		      this.$message({
		  			showClose: true,
		        type: 'info',
		        message: ''
		      });          
		    });
		  },
		  /* */
		  /* */
		  editschool:function(index){
		  			this.tempData=this.deepCopy(this.tableData[index]);
		  			this.tempindex=index;
		  			this.addstate=true;
		  			this.bgstate=true;
		  			/*  */
		  			this.schoolmsg = this.tableData[index];
		  },
		  /* */
		  canceledit:function(){
		  	  this.tableData.splice(this.tempindex,1,this.tempData);
		  	  this.addstate=false;
		  	  this.bgstate=false;
		  	  this.tempData={};
		  	  this.tempindex=-1;
		  },
		   /* TODO*/
		  submitForm(formName) {
		    this.$refs[formName].validate((valid) => {
  if (valid) {
	  this.$confirm(', ?', '', {
	    confirmButtonText: '',
	    cancelButtonText: '',
	    type: 'warning'
	  }).then(() => {
	  	this.addstate=false;
	  	this.bgstate=false;
	  	this.schoolmsg={"id":"111","feedbackusername":"","feedbackname":"","identity":"","idea":"","type":"","ideatime":"2019-02-13"};
	    this.$message({
	  	showClose: true,
	      type: 'success',
	      message: '!'
	    });
	  }).catch(() => {
	    this.$message({
	  	showClose: true,
	      type: 'info',
	      message: ''
	    });          
	  });
  } else {
    console.log('error !!');
    return false;
  }
		    });
		  },
		  resetForm(formName) {
		    this.$refs[formName].resetFields();
		  },
	},
	created:function(){
		  this.$http.get("../../static/idealist.json").then(
				function(res){
					console.log(res.data);
					this.tableData=res.data;
				},
				function(err){
					console.log(err);
				}
		  ),
		  this.sesname = window.sessionStorage.getItem("username");
		  if(!this.sesname){
		     this.$router.push("/index");
		  }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.advisehead{
		background:#F2F2F2;
		height:65px;
		margin-top:20px;
	}
	.adviseinput{
		width:150px;
		height:30px;
	}
	.advisecoat{
		height:30px;
		line-height: 65px;
		margin-left:20px;
	}
	.el-table{
		margin-top:30px;
		border-top:0;
	}
/*  */
.schoolbg{
width:100%;
height:100%;
background: rgba(0,0,0,0.2);
position:fixed;
top:0;
left:0;
z-index:10
}
.schooladdcoat{
	width:50%;
	height:85%;
	position:fixed;
	top:5%;
	left:25%;
	z-index:11;
	background:white;
	padding-left:20px;
	padding-top:22px;
}
.schooladdcoat .el-input{
	width:90%;
}
.schooladdcoat p{
	padding-bottom:20px;
}
.schooladdcoat i{
	float:right;
	margin-right:20px;
}
.schooladdcoat .el-button{
	float:right;
	margin-right:25px;
}

/*  */
.find{
	width:40%;
	height:100%;
	background:white;
	position:fixed;
	left:30%;
	top:0%;
	z-index:11;
	font-size:13px;
}
.find label{
	display:inline-block;
	width:120px;
	text-align:right;
	margin-right:20px;
	margin-left:20px;
}
.find ul{
	margin-top:20px;
}
.find h5{
	margin-top:5px;
	text-indent:20px;
}
.find h5 i{
	float:right;
	margin-right:20px;
}
.find li{
	margin-top:10px;
}
.find li .el-button{
	margin-left:80px;
}
.find .schoolfindbtn{
	margin-left:150px;
}
</style>



