<template>
  <div class="school">
	<el-breadcrumb separator="/">
			<el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
			<el-breadcrumb-item><a href="/"></a></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>			
		<div class="schoolhead">
			<div class="schoolcoat">
				<el-input class="schoolinput" placeholder=""  clearable></el-input>
				<el-button type="primary"></el-button>
				<el-button type="primary"></el-button>
				<el-button type="primary" @click="addschool('ruleForm')" ></el-button> <!-- TODO-->
			</div>
		</div>			
		<el-table :data="tableData" border style="width: 100%;"  @current-change="idHandleCurrentChange">
		<el-table-column  label=""  type="index" width="55"></el-table-column>
		<el-table-column prop="school_name" label=""  width="77"></el-table-column>
		<el-table-column prop="location" label=""  width="77"></el-table-column>
		<el-table-column prop="personcharge" label=""  width="70"></el-table-column>
		<el-table-column prop="phone" label=""  width="77"></el-table-column>
		<el-table-column prop="teachercount" label="" width="77" ></el-table-column>	
		<el-table-column prop="studentcount" label=""  width="77"></el-table-column>
		<el-table-column prop="classcount" label=""  width="77"></el-table-column>
		<el-table-column prop="checktime" label="" width="77" ></el-table-column>	
		<el-table-column prop="state" label=""  width="55"></el-table-column>
		<el-table-column prop="pay" label="" sortable width="110"></el-table-column>
		<el-table-column label="" >
			
		<template slot-scope="scope">
			<el-button size="mini" type="info" @click="findschool(scope.$index)"></el-button>
			<el-button size="mini" @click="editschool(scope.$index)"></el-button>
			<el-button size="mini" type="danger" @click="delschool(scope.$index)"></el-button>	
        </template>
		
		</el-table-column>	
			
		</el-table>
		<div class="schoolfoot">
			<div class="block">
				<el-pagination @size-change="handleSizeChange"
					@current-change="handleCurrentChange"
					:current-page="currentPage4"
					:page-sizes="[10, 20, 30]"
					:page-size="10"
					layout="total, sizes, prev, pager, next, jumper"
					:total="3">
				</el-pagination>
			</div>
		</div>		
		
		<!--  -->
	<div class="schooladdcoat" v-show="addstate">
		<p><label>/</label><i class="el-icon-close" @click="canceledit"></i></p>
		<el-form  :model="schoolmsg" :rules="rules" ref="ruleForm" label-width="110px" class="demo-ruleForm">
			<el-form-item label="" prop="school_name" >
				<el-input v-model="schoolmsg.school_name"></el-input>
			</el-form-item>
			<el-form-item label="" prop="location" >
				<el-select v-model="schoolmsg.location" placeholder="">
					<el-option label="" value=""></el-option>
					<el-option label="1" value="1"></el-option>
					<el-option label="2" value="2"></el-option>
					<el-option label="3" value="3"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item label=""  prop="personcharge" >
				<el-input v-model="schoolmsg.personcharge"></el-input>
			</el-form-item>
			<el-form-item label="" prop="schooladdress" >
				<el-input v-model="schoolmsg.schooladdress"></el-input>
			</el-form-item>
			<el-form-item label="" prop="phone" >
				<el-input v-model="schoolmsg.phone"></el-input>
			</el-form-item>
			<el-form-item label="" prop="desc">
				<el-input type="textarea" style="width:90%;" v-model="schoolmsg.desc"></el-input>
			</el-form-item>
			<el-form-item label="" >
				<el-form-item prop="pay">
				<el-date-picker type="date" placeholder=""  format="yyyy  MM  dd "
      value-format="yyyy-MM-dd" v-model="schoolmsg.pay" style="width:94%;padding-right:20px;"></el-date-picker>
				</el-form-item>
			</el-form-item>
					<div class="schoolbtncoat">
						<el-button type="primary" @click="submitForm('ruleForm')">{{addoredit}}</el-button>
						<el-button @click="resetForm('ruleForm')"></el-button>
					</div>
			</el-form>
		</div>		
		<div class="schoolbg" v-show="bgstate"></div>
			
			
			<!--  -->
			<div class="find"  v-show="findstate">
				<h5><p></p><i class="el-icon-close" @click="closefind"></i></h5>
					<ul >
						<li><label></label>{{schoolmsg.school_name}}</li>
						<li><label></label>{{schoolmsg.location}}</li>
						<li><label></label>{{schoolmsg.personcharge}}</li>
						<li><label></label>{{schoolmsg.teachercount}}<el-button type="primary" size="mini" round @click="linktoteacher" ></el-button></li>
						<li><label></label>{{schoolmsg.studentcount}}<el-button type="primary" size="mini" round @click="linktostudent"></el-button></li>
						<li><label></label>{{schoolmsg.parentcount}}<el-button type="primary" size="mini" round @click="linktoparent"></el-button></li>
						<li><label></label>{{schoolmsg.classcount}}</li>
						<li><label></label>{{schoolmsg.checktime}}</li>
						<li><label></label>{{schoolmsg.pay}}</li>
						<li><label></label>{{schoolmsg.schooladdress}}</li>
						<li><label></label></li>
						<li><label></label>{{schoolmsg.phone}}<el-button type="primary" size="mini" round ></el-button></li>
						<li><label style="float:left;">logo</label>
							<img src="../../static/vue.png" />
						</li>
						<div class="schoolfindbtn">
							<el-button type="primary"></el-button>
							<el-button type="danger" @click="deleteschool"></el-button>
						</div>
					</ul>
			</div>	
		
  </div>
</template>

<script>
export default {
  name: 'School',
  data () {
    return {
		/* / */
		sesname:"",
		tempData:{},
		tempindex:-1,
		addoredit:"",
		findstate:false,
		addstate:false,
		bgstate:false,
		schoolmsg:{id:0,school_name:"",location:"",personcharge:"",phone:"",teachercount:"50",studentcount:"300",classcount:"10",checktime:"2019-05-18",state:"",pay:"",parentcount:130,schooladdress:""},
		tableData: [],
			currentPage4: 1	,
			 ruleForm: {
			location:'',
		    school_name: '',
		    schooladdress: '',
		    personcharge: '',
		    pay: '',
		    schooladdress: '',
		    phone: '',
		  },
		  rules: {
		    school_name: [
		      { required: true, message: '', trigger: 'change' },
		      { min: 3, max: 10, message: ' 3  10 ', trigger: 'change' },
			  {pattern: /^[\u4E00-\u9FA5]+$/, message: ''}
		    ],
		    location: [
		      { required: true, message: '', trigger: 'change' }
		    ],
		    personcharge: [
		      { required: true,  message: '', trigger: 'change' },
			   {pattern: /^[\u4E00-\u9FA5]+$/, message: ''}
		    ],
		    pay: [
		      { required: true, message: '', trigger: 'change' }
		    ],
		    schooladdress: [
		      { required: true, message: '', trigger: 'change' }
		    ],
		    phone: [
		      { required: true, message: '', trigger: 'change' },
			  { pattern: /^1[34578]\d{9}$/, message: '' }
		    ]
		},
    }
  },
  methods:{
	  /* ID*/
	   idHandleCurrentChange(val) {
        this.currentRow = val;
      },
	  /* */
	  deepCopy:function (obj) {
      var result = Array.isArray(obj) ? [] : {};
      for (var key in obj) {
        if (obj.hasOwnProperty(key)) {
          if (typeof obj[key] === 'object' && obj[key]!==null) {
            result[key] = deepCopy(obj[key]);   //
          } else {
            result[key] = obj[key];
          }
        }
      }
      return result;
    },
	/*  */
	deleteschool:function(){
		this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
			 this.tableData.splice(this.tempindex,1);
			this.findstate=false;
			this.bgstate=false;
			this.tempindex=-1;
          this.$message({
			showClose: true,
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
			showClose: true,
            type: 'info',
            message: ''
          });          
        });
	},
	  formatter(row, column) {
	     return row.address;
	   },
	  handleSizeChange(val) {
	     console.log(` ${val} `);
	   },
	   handleCurrentChange(val) {
	     console.log(`: ${val}`);
	   },
	   /* */
	  findschool:function(index){
		  this.findstate=true;
		  this.bgstate=true;
		  this.tempindex=index;
		  this.schoolmsg=this.tableData[index];
	  },
	  closefind:function(){
	  		 this.findstate=false;
	  		 this.bgstate=false;
	  		 this.schoolmsg={id:0,school_name:"",location:"",personcharge:"",phone:"",teachercount:"50",studentcount:"300",classcount:"10",checktime:"2019-05-18",state:"",pay:"",parentcount:130,schooladdress:""};
	  },
	  /* */
	  editschool:function(index){
			this.tempData=this.deepCopy(this.tableData[index]);
			this.tempindex=index;
			this.addoredit="";
			this.addstate=true;
			this.bgstate=true;
			/*  */
			this.schoolmsg = this.tableData[index];
	  },
	  /* */
	  canceledit:function(){
		  if(this.addoredit==""){
			  this.tableData.splice(this.tempindex,1,this.tempData);
			  this.addstate=false;
			  this.bgstate=false;
			  this.tempData={};
			  this.tempindex=-1;
		  }else if(this.addoredit==""){
			  this.addstate=false;
			  this.bgstate=false;
		  }else{
			  console.log("404");
		  }
	  },
	  
	  /* TODO*/
	  submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if(this.addoredit==""){
			  if (valid) {
				  this.$confirm(', ?', '', {
				    confirmButtonText: '',
				    cancelButtonText: '',
				    type: 'warning'
				  }).then(() => {
				  	this.addstate=false;
				  	this.bgstate=false;
				  	this.schoolmsg={id:0,school_name:"",location:"",personcharge:"",phone:"",teachercount:"50",studentcount:"300",classcount:"10",checktime:"2019-05-18",state:"",pay:"",parentcount:130,schooladdress:""};
				    this.$message({
				  	showClose: true,
				      type: 'success',
				      message: '!'
				    });
				  }).catch(() => {
				    this.$message({
				  	showClose: true,
				      type: 'info',
				      message: ''
				    });          
				  });
			  } else {
			    console.log('error !!');
			    return false;
			  }
		  }else if(this.addoredit==""){
			  if (valid) {
				  this.$message({
				 showClose: true,
				   type: 'success',
				   message: '!'
				 });
				this.tableData.unshift(this.schoolmsg);
			  			this.addstate=false;
			  			this.bgstate=false;
						this.schoolmsg={id:0,school_name:"",location:"",personcharge:"",phone:"",teachercount:"50",studentcount:"300",classcount:"10",checktime:"2019-05-18",state:"",pay:"",parentcount:130,schooladdress:""};
			  } else {
			    console.log('error !!');
			    return false;
			  }
		  }
        });
      },
	  resetForm(formName) {
        this.$refs[formName].resetFields();
      },
	  /* */
	  delschool:function(index){
		  this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
			this.tableData.splice(index,1);
          this.$message({
			showClose: true,
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
			showClose: true,
            type: 'info',
            message: ''
          });          
        });
	  },
	  /* */
	  addschool:function(formName){
		  this.$refs[formName].resetFields();
		  this.addoredit="";
		  this.addstate=true;
		  this.bgstate=true;
	  },
	 linktoteacher:function(){
		 this.$router.push("/teacher");
	 },
	 linktostudent:function(){
	 	this.$router.push("/students");
	 },
	 linktoparent:function(){
	 	this.$router.push("/parents");
	 },
  },
  created:function(){
	  this.$http.get("../../static/schoollist.json").then(
			function(res){
				this.tableData=res.data;
			},
			function(err){
				console.log(err);
			}
	  ),
	  this.sesname = window.sessionStorage.getItem("username");
	  if(!this.sesname){
	     this.$router.push("/index");
	  }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.schoolhead{
		background:#F2F2F2;
		height:65px;
		margin-top:20px;
	}
	.schoolinput{
		width:180px;
		height:30px;
	}
	.schoolcoat{
		height:30px;
		line-height: 65px;
		margin-left:20px;
	}
	.el-table{
		margin-top:30px;
		border-top:0;
	}
	/*  */
	.schoolbg{
		width:100%;
		height:100%;
		background: rgba(0,0,0,0.2);
		position:fixed;
		top:0;
		left:0;
		z-index:10
	}
	.schooladdcoat{
		width:50%;
		height:85%;
		position:fixed;
		top:5%;
		left:25%;
		z-index:11;
		background:white;
		padding-left:20px;
		padding-top:22px;
	}
	.schooladdcoat .el-input{
		width:90%;
	}
	.schooladdcoat p{
		padding-bottom:20px;
	}
	.schooladdcoat i{
		float:right;
		margin-right:20px;
	}
	.schooladdcoat .el-button{
		float:right;
		margin-right:25px;
	}
	
	/*  */
	.find{
		width:40%;
		height:100%;
		background:white;
		position:fixed;
		left:30%;
		top:0%;
		z-index:11;
		font-size:13px;
	}
	.find label{
		display:inline-block;
		width:120px;
		text-align:right;
		margin-right:20px;
		margin-left:20px;
	}
	.find ul{
		margin-top:20px;
	}
	.find h5{
		margin-top:5px;
		text-indent:20px;
	}
	.find h5 i{
		float:right;
		margin-right:20px;
	}
	.find li{
		margin-top:10px;
	}
	.find li .el-button{
		margin-left:80px;
	}
	.find .schoolfindbtn{
		margin-left:150px;
	}
</style>




