<template>
  <div class="parents">
		<!--  -->
		<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
				<el-breadcrumb-item><a href="/"></a></el-breadcrumb-item>
				<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>	
		<!--  -->
		<div class="parentshead">
			<div class="parentscoat">
				<el-select v-model="value" clearable placeholder="">
			<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
			</el-option>
				</el-select>
					<el-input class="parentsinput" size="medium" placeholder=""  clearable></el-input>
					<el-input class="parentsinput" size="medium" placeholder=""  clearable></el-input>
				<el-button type="primary"></el-button>
			</div>
		</div>
		<!--  -->
		 <el-table :data="tableData" border style="width: 100%;" :default-sort = "{prop: 'id', order: 'descending'}">
		<el-table-column prop="id" label=""  width="65"></el-table-column>
		<el-table-column prop="parentname" label="" width="200" ></el-table-column>	
		<el-table-column prop="phone" label=""  width="200"></el-table-column>
		<el-table-column prop="studentname" label="" width="200"></el-table-column>
		<el-table-column prop="studentnum" label=""  width="200"></el-table-column>
		<el-table-column prop="school" label=""  width="200"></el-table-column>
		</el-table>
		
		<!--  -->
		<div class="parentsfoot">
			<div class="block">
				<el-pagination @size-change="handleSizeChange"
					@current-change="handleCurrentChange"
					:current-page="currentPage4"
					:page-sizes="[10, 20, 30]"
					:page-size="10"
					layout="total, sizes, prev, pager, next, jumper"
					:total="3">
				</el-pagination>
			</div>
		</div>	
  </div>
</template>

<script>
export default {
  name: 'Parents',
  data () {
    return {
		tableData:[],
		options: [{
		    value: '1',
		    label: ''
		  }, {
		    value: '2',
		    label: ''
		  }, {
		    value: '3',
		    label: ''
		  }, {
		    value: '4',
		    label: ''
		  }, {
		    value: '5',
		    label: ''
		  }],
		  value: '',
		  currentPage4: 1,
		  sesname:''
    }
  },
  methods:{
	  handleSizeChange(val) {
	     console.log(` ${val} `);
	   },
	   handleCurrentChange(val) {
	     console.log(`: ${val}`);
	   },
  },
  created:function(){
  	  this.$http.get("../../static/parentlist.json").then(
  			function(res){
  				console.log(res.data);
  				this.tableData=res.data;
  			},
  			function(err){
  				console.log(err);
  			}
  	  ),
	   this.sesname = window.sessionStorage.getItem("username");
	  if(!this.sesname){
	     this.$router.push("/index");
	  }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.parentshead{
		background:#F2F2F2;
		height:65px;
		margin-top:20px;
	}
	.parentsinput{
		width:150px;
		height:30px;
	}
	.parentscoat{
		height:30px;
		line-height: 65px;
		margin-left:20px;
	}
	.el-table{
		margin-top:30px;
		border-top:0;
	}
</style>



