<template>
  <div class="schoolrec">
			<!--  -->
			<el-breadcrumb separator="/">
					<el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
					<el-breadcrumb-item><a href="/"></a></el-breadcrumb-item>
					<el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>	
			<!--  -->
			<div class="schoolrechead">
				<div class="schoolreccoat">
					<el-select v-model="value" clearable placeholder="">
			<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
					</el-option>
					</el-select>
					<el-button type="primary"></el-button>
				</div>
			</div>
			<!--  -->
			<el-table :data="tableData" border style="width: 100%;" @current-change="handleCurrentChange" >
			<el-table-column type="index" label=""  width="55"></el-table-column>
			<el-table-column prop="region" label="" width="210" ></el-table-column>	
			<el-table-column prop="schoolname" label=""  width="250"></el-table-column>
			<el-table-column prop="updatetime" label=""width="250"></el-table-column>
			<el-table-column label=""  >
				
			<template slot-scope="scope">
				<el-button size="mini" type="info" @click="findschool(scope.$index)"></el-button>
				<el-button size="mini" @click="editschool(scope.$index)"></el-button>
				<el-button size="mini" type="danger" @click="delschool(scope.$index)"></el-button>	
			</template>
				
			</el-table-column>	
			</el-table>
			
			
			
			
			<div class="schoolbg" v-show="bgstate"></div>
				
				
				<!--  -->
				<div class="find"  v-show="findstate">
					<h5><p></p><i class="el-icon-close" @click="closefind"></i></h5>
						<ul >
							<li><label></label>{{schoolmsg.schoolname}}</li>
							<li><label></label>{{schoolmsg.region}}</li>
							<li><label></label>{{schoolmsg.updatetime}}</li>
							<li><label style="float:left;">logo</label>
								<img src="../../static/vue.png" />
							</li>
							<div class="schoolfindbtn">
								<el-button type="primary"></el-button>
								<el-button type="danger" @click="deleteschool"></el-button>
							</div>
						</ul>
				</div>	
			
			<!--  -->
			<div class="schooladdcoat" v-show="addstate">
				<p><label></label><i class="el-icon-close" @click="canceledit"></i></p>
				<el-form  :model="schoolmsg" :rules="rules" ref="ruleForm" label-width="110px" class="demo-ruleForm">
					<el-form-item label="" prop="schoolname" >
						<el-input v-model="schoolmsg.schoolname"></el-input>
					</el-form-item>
					<el-form-item label="" prop="region" >
						<el-select v-model="schoolmsg.region" placeholder="">
							<el-option label="" value=""></el-option>
							<el-option label="1" value="1"></el-option>
							<el-option label="2" value="2"></el-option>
							<el-option label="3" value="3"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="" >
						<el-form-item prop="updatetime">
						<el-date-picker type="date" placeholder=""  format="yyyy  MM  dd "
			  value-format="yyyy-MM-dd" v-model="schoolmsg.updatetime" style="width:94%;padding-right:20px;"></el-date-picker>
						</el-form-item>
					</el-form-item>
							<div class="schoolbtncoat">
								<el-button type="primary" @click="submitForm('ruleForm')"></el-button>
								<el-button @click="resetForm('ruleForm')"></el-button>
							</div>
					</el-form>
				</div>		
			
  </div>
</template>

<script>
export default {
  name: 'SchoolRec',
  data () {
    return {
		tableData:[],
		options: [{
		    value: '1',
		    label: ''
		  }, {
		    value: '2',
		    label: '111'
		  }, {
		    value: '3',
		    label: '222'
		  }, {
		    value: '4',
		    label: '333'
		  }, {
		    value: '5',
		    label: '444'
		  }],
		  value: '',
		tempData:{},
		tempindex:-1,
		/*  */
		findstate:false,
		addstate:false,
		bgstate:false,
		sesname:'',
		schoolmsg:{"id":"0","region":"","schoolname":"","updatetime":"" },
			 ruleForm: {
			region:'',
		    schoolname: '',
		    updatetime: '',
		  },
		  rules: {
		    schoolname: [
		      { required: true, message: '', trigger: 'change' },
		      { min: 3, max: 10, message: ' 3  10', trigger: 'change' },
			  {pattern: /^[\u4E00-\u9FA5]+$/, message: ''}
		    ],
		    region: [
		      { required: true, message: '', trigger: 'change' }
		    ],
		    updatetime: [
		      { required: true, message: '', trigger: 'change' }
		    ],
		},
    }
  },
  methods:{
	   /* ID*/
	   handleCurrentChange(val) {
	    this.currentRow = val;
	  },
	   /* */
	    deepCopy:function (obj) {
	    var result = Array.isArray(obj) ? [] : {};
	    for (var key in obj) {
	      if (obj.hasOwnProperty(key)) {
	        if (typeof obj[key] === 'object' && obj[key]!==null) {
	          result[key] = deepCopy(obj[key]);   //
	        } else {
	          result[key] = obj[key];
	        }
	      }
	    }
	    return result;
	  },
	  
	     /* */
	  findschool:function(index){
	  		  this.findstate=true;
	  		  this.bgstate=true;
	  		  this.tempindex=index;
	  		  this.schoolmsg=this.tableData[index];
	  },
	  closefind:function(){
	  		 this.findstate=false;
	  		 this.bgstate=false;
	  		 this.schoolmsg={"id":"0","region":"","schoolname":"","updatetime":"" };
	  },
	 /* */
	  deleteschool:function(){
	  	this.$confirm(', ?', '', {
	        confirmButtonText: '',
	        cancelButtonText: '',
	        type: 'warning'
	      }).then(() => {
	  		 this.tableData.splice(this.tempindex,1);
	  		this.findstate=false;
	  		this.bgstate=false;
	  		this.tempindex=-1;
	        this.$message({
	  		showClose: true,
	          type: 'success',
	          message: '!'
	        });
	      }).catch(() => {
	        this.$message({
	  		showClose: true,
	          type: 'info',
	          message: ''
	        });          
	      });
	  },
	   /* */
	  delschool:function(index){
	  		  this.$confirm(', ?', '', {
	      confirmButtonText: '',
	      cancelButtonText: '',
	      type: 'warning'
	    }).then(() => {
	  			this.tableData.splice(index,1);
	      this.$message({
	  			showClose: true,
	        type: 'success',
	        message: '!'
	      });
	    }).catch(() => {
	      this.$message({
	  			showClose: true,
	        type: 'info',
	        message: ''
	      });          
	    });
	  },
	  /* */
	  /* */
	  editschool:function(index){
	  			this.tempData=this.deepCopy(this.tableData[index]);
	  			this.tempindex=index;
	  			this.addstate=true;
	  			this.bgstate=true;
	  			/*  */
	  			this.schoolmsg = this.tableData[index];
	  },
	  /* */
	  canceledit:function(){
	  	  this.tableData.splice(this.tempindex,1,this.tempData);
	  	  this.addstate=false;
	  	  this.bgstate=false;
	  	  this.tempData={};
	  	  this.tempindex=-1;
	  },
	   /* TODO*/
	  submitForm(formName) {
	    this.$refs[formName].validate((valid) => {
	  			  if (valid) {
	  				  this.$confirm(', ?', '', {
	  				    confirmButtonText: '',
	  				    cancelButtonText: '',
	  				    type: 'warning'
	  				  }).then(() => {
	  				  	this.addstate=false;
	  				  	this.bgstate=false;
	  				  	this.schoolmsg={"id":"0","region":"","schoolname":"","updatetime":"" };
	  				    this.$message({
	  				  	showClose: true,
	  				      type: 'success',
	  				      message: '!'
	  				    });
	  				  }).catch(() => {
	  				    this.$message({
	  				  	showClose: true,
	  				      type: 'info',
	  				      message: ''
	  				    });          
	  				  });
	  			  } else {
	  			    console.log('error !!');
	  			    return false;
	  			  }
	    });
	  },
	  resetForm(formName) {
	    this.$refs[formName].resetFields();
	  },
  },
	 created:function(){
		  this.$http.get("../../static/schoolintroduction.json").then(
				function(res){
					console.log(res.data);
					this.tableData=res.data;
				},
				function(err){
					console.log(err);
				}
		  ),
		   this.sesname = window.sessionStorage.getItem("username");
		  if(!this.sesname){
		     this.$router.push("/index");
		  }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.schoolrechead{
		background:#F2F2F2;
		height:65px;
		margin-top:20px;
	}
	.schoolrecinput{
		width:180px;
		height:30px;
	}
	.schoolreccoat{
		height:30px;
		line-height: 65px;
		margin-left:20px;
	}
	.el-table{
		margin-top:30px;
		border-top:0;
	}
	
	
	
	/*  */
	.schoolbg{
		width:100%;
		height:100%;
		background: rgba(0,0,0,0.2);
		position:fixed;
		top:0;
		left:0;
		z-index:10
	}
	.schooladdcoat{
		width:50%;
		height:85%;
		position:fixed;
		top:5%;
		left:25%;
		z-index:11;
		background:white;
		padding-left:20px;
		padding-top:22px;
	}
	.schooladdcoat .el-input{
		width:90%;
	}
	.schooladdcoat p{
		padding-bottom:20px;
	}
	.schooladdcoat i{
		float:right;
		margin-right:20px;
	}
	.schooladdcoat .el-button{
		float:right;
		margin-right:25px;
	}
	
	/*  */
	.find{
		width:40%;
		height:100%;
		background:white;
		position:fixed;
		left:30%;
		top:0%;
		z-index:11;
		font-size:13px;
	}
	.find label{
		display:inline-block;
		width:120px;
		text-align:right;
		margin-right:20px;
		margin-left:20px;
	}
	.find ul{
		margin-top:20px;
	}
	.find h5{
		margin-top:5px;
		text-indent:20px;
	}
	.find h5 i{
		float:right;
		margin-right:20px;
	}
	.find li{
		margin-top:10px;
	}
	.find li .el-button{
		margin-left:80px;
	}
	.find .schoolfindbtn{
		margin-left:150px;
	}
</style>



