<template>
  <div class="ads">
    
    <!--  -->
    <el-breadcrumb separator="/">
    		<el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
    		<el-breadcrumb-item><a href="/"></a></el-breadcrumb-item>
    		<el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>	
    <!--  -->
    <div class="adshead">
    	<div class="adscoat">
    			<el-input class="adsinput" size="medium" placeholder=""  clearable></el-input>
    		<el-button type="primary"></el-button>
    		<el-button type="primary"  @click="addschool('ruleForm')"></el-button>
    	</div>
    </div>
    <!--  -->
    <el-table :data="tableData" border style="width: 100%;"  @current-change="idHandleCurrentChange">
    <el-table-column type="index" label=""  width="65"></el-table-column>
    <el-table-column prop="name" label="" width="100" ></el-table-column>	
    <el-table-column prop="date" label=""  width="100">
				 <!---->
			<template slot-scope="scope">
			  <img  src="../../static/monkey.jpg" alt="" style="width: 50px;height: 50px">
			</template>
		</el-table-column>
    <el-table-column prop="href" label="" width="100"></el-table-column>
    <el-table-column prop="readnum" label=""  width="100"></el-table-column>
    <el-table-column prop="operationuser" label=""  width="100"></el-table-column>
    <el-table-column prop="" label="" width="100"></el-table-column>
    <el-table-column prop="state" label="" width="100"></el-table-column>
    <el-table-column prop="date" label="">
				<template slot-scope="scope">
					<el-button size="mini" type="info" @click="findschool(scope.$index)"></el-button>
					<el-button size="mini" @click="editschool(scope.$index)"></el-button>
					<el-button size="mini" type="danger" @click="delschool(scope.$index)"></el-button>	
				</template>
		</el-table-column>
    </el-table>
    
    <!--  -->
    <div class="adsfoot">
    	<div class="block">
    		<el-pagination @size-change="handleSizeChange"
    			@current-change="handleCurrentChange"
    			:current-page="currentPage4"
    			:page-sizes="[10, 20, 30]"
    			:page-size="10"
    			layout="total, sizes, prev, pager, next, jumper"
    			:total="3">
    		</el-pagination>
    	</div>
    </div>	
    <!--  -->
    <div class="find"  v-show="findstate">
    	<h5><p></p><i class="el-icon-close" @click="closefind"></i></h5>
    		<ul >
    			<li><label></label>{{schoolmsg.name}}</li>
    			<li><label></label>{{schoolmsg.href}}</li>
    			<li><label></label>{{schoolmsg.readnum}}</li>
    			<li><label></label>{{schoolmsg.operationuser}}</li>
    			<li><label></label>{{schoolmsg.state}}</li>
    			<li><label style="float:left;"></label>
    				<img src="../../static/vue.png" />
    			</li>
    			<div class="schoolfindbtn">
    				<el-button type="primary"></el-button>
    				<el-button type="danger" @click="deleteschool"></el-button>
    			</div>
    		</ul>
    </div>	
    
    
    <!--  -->
    <div class="newsbg" v-show="bgstate"></div>
    <!--  -->
    <div class="newsaddcoat" v-show="addstate">
    	<p class="newsaddhead"><label>/</label><i class="el-icon-close" @click="canceledit"></i></p>
    	<el-form  :model="schoolmsg" :rules="rules" ref="ruleForm" label-width="110px" class="demo-ruleForm">
    		<el-form-item label="" prop="name" required>
    			<el-input v-model="schoolmsg.name"></el-input>
    		</el-form-item>
    		
    		<!--  -->
    		<el-form-item label="">
    			<el-upload
    			  class="avatar-uploader123"
    			  action="https://jsonplaceholder.typicode.com/posts/"
    			  :show-file-list="false"
    			  :on-success="handleAvatarSuccess"
    			  :before-upload="beforeAvatarUpload">
    			  <img v-if="imageUrl" :src="imageUrl" class="avatar">
    			  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
    			</el-upload>
    		</el-form-item>
    		
    		<el-form-item label="" >
    			<el-upload
    			action="https://jsonplaceholder.typicode.com/posts/"
    			list-type="picture-card"
    			:on-preview="handlePictureCardPreview"
    			:on-remove="handleRemove">
    			<i class="el-icon-plus"></i>
    		</el-upload>
    		<el-dialog :visible.sync="dialogVisible">
    			<img width="100%" :src="dialogImageUrl" alt="">
    		</el-dialog>
    		</el-form-item>
    		
    		
    		
    		<el-form-item label="" prop="region">
    			<el-select v-model="schoolmsg.region" placeholder="">
    				<el-option label="" value=""></el-option>
    				<el-option label="1" value="1"></el-option>
    				<el-option label="2" value="2"></el-option>
    			</el-select>
    			<el-select v-model="schoolmsg.school" placeholder="">
    				<el-option label="" value=""></el-option>
    				<el-option label="1" value="1"></el-option>
    				<el-option label="2" value="2"></el-option>
    			</el-select>
    		</el-form-item>
    		
    		<el-form-item label="" prop="desc">
    			<el-input type="textarea" style="width:90%;" v-model="ruleForm.desc"></el-input>
    		</el-form-item>
    		
    		</el-form-item>
    				<div class="newsbtncoat">
    					<el-button type="primary" @click="submitForm('ruleForm')" >{{addoredit}}</el-button>
    					<el-button @click="resetForm('ruleForm')"></el-button>
    				</div>
    		</el-form>
    </div>
   
  </div>
</template>

<script>
export default {
  name: 'Ad',
  data () {
    return {
			tableData:[],
		options: [{
		    value: '1',
		    label: ''
		  }, {
		    value: '2',
		    label: ''
		  }, {
		    value: '3',
		    label: ''
		  }, {
		    value: '4',
		    label: ''
		  }, {
		    value: '5',
		    label: ''
		  }],
			currentPage4: 1	,
		  value: '',
			ruleForm: {
			  name: '',
			  region: '',
			},
			rules: {
			    name: [
			      { required: true, message: '', trigger: 'change' },
			      { min: 3, max:10, message: ' 3  10', trigger: 'change' },
						{pattern: /^[\u4E00-\u9FA5]+$/, message: ''}
			    ],
			    region: [
			      { required: true, message: '', trigger: 'change' }
			    ],
			},
		schoolmsg:{"id":"11","name":"","href":"www.baidu.com","readnum":"100","operationuser":"","state":""},
		imageUrl: '',
		dialogImageUrl: '',
		dialogVisible: false,
		tempData:{},
		tempindex:-1,
		/*  */
		findstate:false,
		addstate:false,
		bgstate:false,
		addoredit:"",	
		sesname:''
    }
  },
	methods:{
		/* ID*/
		 idHandleCurrentChange(val) {
		  this.currentRow = val;
		},
		handleSizeChange(val) {
		   console.log(` ${val} `);
		 },
		 handleCurrentChange(val) {
		   console.log(`: ${val}`);
		 },
		 handleAvatarSuccess(res, file) {
		   this.imageUrl = URL.createObjectURL(file.raw);
		 },
		 beforeAvatarUpload(file) {
		   const isJPG = file.type === 'image/jpeg';
		   const isLt2M = file.size / 1024 / 1024 < 2;
		
		   if (!isJPG) {
		     this.$message.error(' JPG !');
		   }
		   if (!isLt2M) {
		     this.$message.error(' 2MB!');
		   }
		   return isJPG && isLt2M;
		 },
		handleRemove(file, fileList) {
		   console.log(file, fileList);
		 },
		 handlePictureCardPreview(file) {
		   this.dialogImageUrl = file.url;
		   this.dialogVisible = true;
		 },
		  /* */
		 	    deepCopy:function (obj) {
		 	    var result = Array.isArray(obj) ? [] : {};
		 	    for (var key in obj) {
		 	      if (obj.hasOwnProperty(key)) {
		 	        if (typeof obj[key] === 'object' && obj[key]!==null) {
		 	          result[key] = deepCopy(obj[key]);   //
		 	        } else {
		 	          result[key] = obj[key];
		 	        }
		 	      }
		 	    }
		 	    return result;
		 	  },
		 	  
		 	     /* */
		 	  findschool:function(index){
		 	  		  this.findstate=true;
		 	  		  this.bgstate=true;
		 	  		  this.tempindex=index;
		 	  		  this.schoolmsg=this.tableData[index];
		 	  },
		 	  closefind:function(){
		 	  		 this.findstate=false;
		 	  		 this.bgstate=false;
		 	  		 this.schoolmsg={"id":"11","name":"","href":"www.baidu.com","readnum":"100","operationuser":"","state":""};
		 	  },
		 	 /* */
		 	  deleteschool:function(){
		 	  	this.$confirm(', ?', '', {
		 	        confirmButtonText: '',
		 	        cancelButtonText: '',
		 	        type: 'warning'
		 	      }).then(() => {
		 	  		 this.tableData.splice(this.tempindex,1);
		 	  		this.findstate=false;
		 	  		this.bgstate=false;
		 	  		this.tempindex=-1;
		 	        this.$message({
		 	  		showClose: true,
		 	          type: 'success',
		 	          message: '!'
		 	        });
		 	      }).catch(() => {
		 	        this.$message({
		 	  		showClose: true,
		 	          type: 'info',
		 	          message: ''
		 	        });          
		 	      });
		 	  },
		 	   /* */
		 	  delschool:function(index){
		 	  		  this.$confirm(', ?', '', {
		 	      confirmButtonText: '',
		 	      cancelButtonText: '',
		 	      type: 'warning'
		 	    }).then(() => {
		 	  			this.tableData.splice(index,1);
		 	      this.$message({
		 	  			showClose: true,
		 	        type: 'success',
		 	        message: '!'
		 	      });
		 	    }).catch(() => {
		 	      this.$message({
		 	  			showClose: true,
		 	        type: 'info',
		 	        message: ''
		 	      });          
		 	    });
		 	  },
		 	  /* */
		 	  /* */
		 	  editschool:function(index){
		 	  			this.tempData=this.deepCopy(this.tableData[index]);
		 	  			this.tempindex=index;
		 	  			this.addoredit="";
		 	  			this.addstate=true;
		 	  			this.bgstate=true;
		 	  			/*  */
		 	  			this.schoolmsg = this.tableData[index];
		 	  },
		 	  /* */
		 	  canceledit:function(){
		 	  		  if(this.addoredit==""){
		 	  			  this.tableData.splice(this.tempindex,1,this.tempData);
		 	  			  this.addstate=false;
		 	  			  this.bgstate=false;
		 	  			  this.tempData={};
		 	  			  this.tempindex=-1;
		 	  		  }else if(this.addoredit==""){
		 	  			  this.addstate=false;
		 	  			  this.bgstate=false;
		 	  		  }else{
		 	  			  console.log("404");
		 	  		  }
		 	  },
		 	  
		 	  /* TODO*/
		 	  submitForm(formName) {
		 	    this.$refs[formName].validate((valid) => {
		 	      if(this.addoredit==""){
		 	  			  if (valid) {
		 	  				  this.$confirm(', ?', '', {
		 	  				    confirmButtonText: '',
		 	  				    cancelButtonText: '',
		 	  				    type: 'warning'
		 	  				  }).then(() => {
		 	  				  	this.addstate=false;
		 	  				  	this.bgstate=false;
		 	  				  	this.schoolmsg={"id":"11","name":"","href":"www.baidu.com","readnum":"100","operationuser":"","state":""};
		 	  				    this.$message({
		 	  				  	showClose: true,
		 	  				      type: 'success',
		 	  				      message: '!'
		 	  				    });
		 	  				  }).catch(() => {
		 	  				    this.$message({
		 	  				  	showClose: true,
		 	  				      type: 'info',
		 	  				      message: ''
		 	  				    });          
		 	  				  });
		 	  			  } else {
		 	  			    console.log('error !!');
		 	  			    return false;
		 	  			  }
		 	  		  }else if(this.addoredit==""){
		 	  			  if (valid) {
		 	  				  this.$message({
		 	  				 showClose: true,
		 	  				   type: 'success',
		 	  				   message: '!'
		 	  				 });
		 	  				this.tableData.unshift(this.schoolmsg);
		 	  			  			this.addstate=false;
		 	  			  			this.bgstate=false;
		 	  						this.schoolmsg={"id":"11","name":"","href":"www.baidu.com","readnum":"100","operationuser":"","state":""};
		 	  			  } else {
		 	  			    console.log('error !!');
		 	  			    return false;
		 	  			  }
		 	  		  }
		 	    });
		 	  },
		 	  resetForm(formName) {
		 	    this.$refs[formName].resetFields();
		 	  },
		 	   /* */
		 	  addschool:function(formName){
		 	  		  this.$refs[formName].resetFields();
		 	  		  this.addoredit="";
		 	  		  this.addstate=true;
		 	  		  this.bgstate=true;
		 	  },
	},
	created:function(){
		  this.$http.get("../../static/ad.json").then(
				function(res){
					this.tableData=res.data;
				},
				function(err){
					console.log(err);
				}
		  ),
			 this.sesname = window.sessionStorage.getItem("username");
			if(!this.sesname){
			   this.$router.push("/index");
			}
	},
	
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 .adshead{
 	background:#F2F2F2;
 	height:65px;
 	margin-top:20px;
 }
 .adsinput{
 	width:150px;
 	height:30px;
 }
 .adscoat{
 	height:30px;
 	line-height: 65px;
 	margin-left:20px;
 }
 .el-table{
 	margin-top:30px;
 	border-top:0;
 }
 
 
 
 .newsbg{
		width:100%;
		height:100%;
		background: rgba(0,0,0,0.2);
		position:fixed;
		top:0;
		left:0;
		z-index:10
	}
	.newsaddcoat{
		width:70%;
		height:100%;
		position:fixed;
		top:0%;
		left:14%;
		z-index:11;
		background:white;
		padding-left:20px;
	}
	.newsaddhead i{
		float:right;
		margin-right:20px;
	}
	.newsaddcoat .el-button{
		float:right;
		margin-right:25px;
	}
	.newsaddcoat .el-input{
		width:90%;
	}
 /*  */
 .avatar-uploader123 {
   border: 1px dashed #d9d9d9;
   width:178px;
   border-radius: 6px;
   cursor: pointer;
   position: relative;
   overflow: hidden;
 }
 .avatar-uploader123:hover {
   border-color: #409EFF;
 }
 .avatar-uploader-icon {
   font-size: 28px;
   color: #8c939d;
   width: 178px;
   height: 178px;
   line-height: 178px;
   text-align: center;
 }
 .avatar {
   width: 178px;
   height: 178px;
   display: block;
 }
 /*  */
 .find{
 	width:40%;
 	height:100%;
 	background:white;
 	position:fixed;
 	left:30%;
 	top:0%;
 	z-index:11;
 	font-size:13px;
 }
 .find label{
 	display:inline-block;
 	width:120px;
 	text-align:right;
 	margin-right:20px;
 	margin-left:20px;
 }
 .find ul{
 	margin-top:20px;
 }
 .find h5{
 	margin-top:5px;
 	text-indent:20px;
 }
 .find h5 i{
 	float:right;
 	margin-right:20px;
 }
 .find li{
 	margin-top:10px;
 }
 .find li .el-button{
 	margin-left:80px;
 }
 .find .schoolfindbtn{
 	margin-left:150px;
 }
</style>



